




















































































































import { Vue, Component } from 'vue-property-decorator';

import { ServiceResolveText } from '@/../helpers';

import FormImageDark from '@/../components/image/image_dark.vue';
import FormImageLight from '@/../components/image/image_light.vue';
import FormImageAdvanced from '@/../components/image/image_advanced.vue';
import FormImageAdvancedCropper from '@/../components/image/image_advanced_cropper.vue';
import FormImageAdvancedCropperButton from '@/../components/image/image_advanced_cropper_button.vue';
import FormImageAdvancedCropperLarge from '@/../components/image/image_advanced_cropper_large.vue';
import FormImageMultiple from '@/../components/image/image_multiple.vue';
import ImageView from '@/../components/image/image_view.vue';
import { FakeData } from '@/helpers/fake-data';

// form
interface IFormItem {}

interface IFormData {
    objectId: string;
    name: string;
    image_view: string;
    design_advanced: string;
    design_advanced_cropper: string;
    design_advanced_cropper_large: string;
    image_multiples: string[];
    design: string;
    dark_x: string;
    light_x: string;
}

interface ISelectOptionItem {}

interface IFormDataDisabled {}

interface IFormRelationSelectConfigs {}

interface IFormButtonDisabled {}

@Component({
    components: {
        FormImageDark,
        FormImageLight,
        FormImageAdvanced,
        ImageView,
        FormImageAdvancedCropper,
        FormImageAdvancedCropperButton,
        FormImageAdvancedCropperLarge,
        FormImageMultiple,
    },
})
export default class VuePageClass extends Vue {
    private imageBase64: string = FakeData.FEMALE_IMG;

    private formDataOriginal: IFormData = {
        objectId: '',
        name: '',
        image_view: '',
        design_advanced: '',
        design_advanced_cropper: '',
        design_advanced_cropper_large: '',
        image_multiples: [],
        design: '',
        dark_x: '',
        light_x: '',
    };
    private formData: IFormData = { ...this.formDataOriginal };

    private formUploadImage(src: string): void {}

    private formUploadImages(srcs: string[]): void {
        this.formData.image_multiples = srcs;
    }

    private formImageMultiple(): void {
        console.log('image_multiples => ', this.formData.image_multiples);
    }

    //////////////////// Resolve text ////////////////////
    private resolvePhotoIsUrlOrBase64(data: string) {
        return ServiceResolveText.resolvePhotoIsUrlOrBase64(data);
    }

    //////////////////// Modal ////////////////////
}
