














































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import { IComponent } from '@/../models';

// VueCropper
import VueCropper from 'vue-cropper';
Vue.use(VueCropper);

// vue-easy-lightbox
import Lightbox from 'vue-easy-lightbox';
Vue.use(Lightbox);

interface ILightboxItem {
    imgs: string;
    visible: boolean;
    index: number;
}

interface IOption extends IComponent.ICropItem {
    img: string;
}

@Component({
    components: {},
})
export default class FormImageCropper extends Vue {
    //////////////////// prop ////////////////////

    @Prop({
        type: Object,
        default: () => {},
    })
    private cropItem: IComponent.ICropItem;

    @Prop({
        type: String,
        default: () => undefined,
    })
    private imgFile: string;

    //////////////////// variable ////////////////////
    private rangeValue: number = 0;
    private showProportion: boolean = false;

    private imageUrl: string = '';

    //剪切圖片上傳
    private previews: any = {};
    private option: IOption = {
        img: '', // 裁剪圖片的地址

        size: 1, //剪切後的圖片質量（0.1 - 1）
        outputType: 'png', // 裁剪生成圖片的格式 jpeg (jpg 需要传入jpeg) || png || webp

        full: true, //是否輸出原圖比例的截圖

        canScale: false, // 圖片是否允許滾輪縮放
        canMove: true, // 上傳圖片是否可以移動
        canMoveBox: true, // 截圖框能否拖動

        autoCrop: false, // 是否默認生成截圖框
        autoCropWidth: 150, // 默認生成截圖框寬度（0 - max）
        autoCropHeight: 150, // 默認生成截圖框高度（0 - max）

        fixed: true, // 是否開啟截圖框寬高固定比例
        fixedNumber: [1, 1], // 截圖框的寬高比例 [寬度, 高度]
        fixedBox: true, // 固定截圖框大小 不允許改變

        original: false, // 上傳圖片按照原始比例渲染

        centerBox: true, // 截圖框是否被限制在圖片裡面

        info: true, // 裁剪框的大小信息
        infoTrue: true, // true 為展示真實輸出圖片寬高 false 展示看到的截圖框寬高

        maxImgSize: 2000, // 0 - max 限制圖片最大寬度和高度
    };

    //////////////////// vue life ////////////////////

    // 參考資料：https://blog.csdn.net/qq_39800789/article/details/105615181

    private created(): void {}
    private mounted(): void {}

    @Watch('cropItem', { immediate: true, deep: true })
    private cropItemChanged(newVal: IComponent.ICropItem, oldVal: IComponent.ICropItem): void {
        this.option.size = newVal.size;
        this.option.outputType = newVal.outputType;
        this.option.full = newVal.full;
        this.option.canScale = newVal.canScale;
        this.option.canMove = newVal.canMove;
        this.option.canMoveBox = newVal.canMoveBox;
        this.option.autoCrop = newVal.autoCrop;
        this.option.autoCropWidth = newVal.autoCropWidth;
        this.option.autoCropHeight = newVal.autoCropHeight;
        this.option.fixed = newVal.fixed;
        this.option.fixedNumber = newVal.fixedNumber;
        this.option.fixedBox = newVal.fixedBox;
        this.option.original = newVal.original;
        this.option.centerBox = newVal.centerBox;
        this.option.info = newVal.info;
        this.option.infoTrue = newVal.infoTrue;
        this.option.maxImgSize = newVal.maxImgSize;

        this.changeLayer();
    }

    @Watch('imgFile', { immediate: true, deep: true })
    private imgFileChanged(newVal: string, oldVal: string): void {
        this.option.img = newVal;
        this.imageUrl = newVal;
    }

    @Watch('rangeValue', { immediate: true, deep: true })
    private rangeValueChanged(newVal: string, oldVal: string): void {
        let diff: number = parseInt(newVal, 10) - parseInt(oldVal, 10);
        if (!isNaN(diff)) {
            this.changeScale(diff);
        }
    }

    //////////////////// 將值傳遞給父元件 ////////////////////
    private sendValueToParent(): void {
        this.$emit('cropper-value', this.imageUrl);
    }

    //////////////////// 上傳圖片 ////////////////////
    private submitPhoto(): void {
        let cropper: any = this.$refs.cropper;
        // 獲取截圖的base64 數據
        cropper.getCropData((data) => {
            this.imageUrl = data;
            this.sendValueToParent();
        });
    }

    //////////////////// * 圖片調整開始 * ////////////////////
    //放大/縮小
    private changeScale(num: number): void {
        let cropper: any = this.$refs.cropper;
        num = num || 1;
        cropper.changeScale(num);
    }

    //左旋轉
    private rotateLeft(): void {
        let cropper: any = this.$refs.cropper;
        cropper.rotateLeft();
    }

    //右旋轉
    private rotateRight(): void {
        let cropper: any = this.$refs.cropper;
        cropper.rotateRight();
    }

    // 選擇範圍大小
    private changeLayer(): void {
        let cropper: any = this.$refs.cropper;
        this.option.autoCrop = !this.option.autoCrop;
    }

    // 實時預覽函數
    private realTime(data: any): void {
        this.previews = data;
    }

    private imgLoad(message: string): void {
        // console.log('imgLoad => ', message);
    }

    //////////////////// * 圖片調整結束 * ////////////////////

    //下載圖片
    private downloadImage(type): void {
        let cropper: any = this.$refs.cropper;
        var aLink = document.createElement('a');
        aLink.download = 'photo';
        if (type === 'blob') {
            cropper.getCropBlob((data) => {
                aLink.href = window.URL.createObjectURL(data);
                aLink.click();
            });
        } else {
            cropper.getCropData((data) => {
                aLink.href = data;
                aLink.click();
            });
        }
    }

    // 壓縮圖片
    private compressImage(img): string {
        let canvas = document.createElement('canvas'),
            ctx = canvas.getContext('2d'),
            initSize = img.src.length,
            width = img.width,
            height = img.height;
        canvas.width = width;
        canvas.height = height;
        // 鋪底色
        ctx.fillStyle = '#fff';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(img, 0, 0, width, height);
        //進行壓縮
        let ndata = canvas.toDataURL('image/jpeg', 0.8);
        //console.log("壓縮後的圖片大小：" + ndata.length)
        return ndata;
    }

    // base64轉成bolb對像
    private dataURItoBlob(base64Data): Blob {
        let byteString;
        if (base64Data.split(',')[0].indexOf('base64') >= 0) byteString = atob(base64Data.split(',')[1]);
        else byteString = unescape(base64Data.split(',')[1]);
        let mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
        let ia = new Uint8Array(byteString.length);
        for (let i = 0; i < byteString.length; i++) {
            ia[i] = byteString.charCodeAt(i);
        }
        return new Blob([ia], { type: mimeString });
    }

    //////////////////// 選擇比例 ////////////////////
    private slectProportion() {
        this.showProportion = !this.showProportion;
        this.changeLayer();
    }

    private slect43() {
        this.option.fixedNumber = [4, 3];
        this.option.fixed = true;
        this.showProportion = !this.showProportion;
        this.changeLayer();
    }

    private slect169() {
        this.option.fixedNumber = [16, 9];
        this.option.fixed = true;
        this.showProportion = !this.showProportion;
        this.changeLayer();
    }

    private slectSquare() {
        this.option.fixedNumber = [1, 1];
        this.option.fixed = true;
        this.showProportion = !this.showProportion;
        this.changeLayer();
    }

    private slectFree() {
        this.option.fixedNumber = [1, 1];
        this.option.fixed = false;
        this.showProportion = !this.showProportion;
        this.changeLayer();
    }
}
