
















































import { Vue, Component } from 'vue-property-decorator';

import { ServiceResolveText, personEmpty } from '@/../helpers';
import FormImageAdvancedCropperLarge from '@/../components/image/image_advanced_cropper_large.vue';
import { ModelsImage as Model } from './index';

//#region import swiper
// use swiper 6.x will have pagination and navigation not work issue, there the ways to fix this issue
import { Swiper as SwiperClass, Pagination, Navigation, Mousewheel, Autoplay, SwiperOptions as ISwiperOptions } from 'swiper/swiper.esm';
import getAwesomeSwiper from 'vue-awesome-swiper/dist/exporter';

SwiperClass.use([Pagination, Mousewheel, Navigation, Autoplay]);
Vue.use(getAwesomeSwiper(SwiperClass));
const { Swiper, SwiperSlide } = getAwesomeSwiper(SwiperClass);

// import style
import 'swiper/swiper-bundle.min.css';
//#endregion import swiper

@Component({
    components: { FormImageAdvancedCropperLarge },
})
export default class FormImageMultiple extends Vue {
    //#region variables

    private swiperOption: ISwiperOptions = {
        slidesPerView: 3,
        centeredSlides: false,
        spaceBetween: 30,

        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
    };

    private imageItem: Model.IImageItem = {
        selectedImage: personEmpty,
        isClearSrc: false,
    };

    private swiperSlides: Model.ISwiper[] = [Model.FirstSwiperItem];
    // private swiperSlides: Model.ISwiper[] = Model.FakeData;
    //#endregion variables

    //#region form function
    private formClickImage(index: number): void {
        // click add new image
        if (index === Model.LastIndex) {
            this.imageItem.selectedImage = personEmpty;
            this.imageItem.isClearSrc = true;
            this.swiperSlides.forEach((swiper: Model.ISwiper) => (swiper.isSelectd = false));
            const item: Model.ISwiper = { src: personEmpty, index: this.swiperSlides.length - 1, isSelectd: true };
            this.swiperSlides.splice(-1, 0, item);
            this.swiperSlides.pop();
        } else {
            // click selected image
            this.swiperSlides.forEach((swiper: Model.ISwiper) => (swiper.isSelectd = false));
            this.swiperSlides[index].isSelectd = true;
            if (this.swiperSlides[index].src === personEmpty) {
                this.imageItem.selectedImage = personEmpty;
                this.imageItem.isClearSrc = true;
            } else {
                this.imageItem.selectedImage = this.swiperSlides[index].src;
                this.imageItem.isClearSrc = false;
            }
        }
    }

    private formUploadImage(src: string): void {
        this.imageItem.isClearSrc = false;
        let selectedItem: Model.ISwiper;

        // get selected src
        (this.swiperSlides || []).forEach((swiper: Model.ISwiper, index: number) => {
            if (swiper.isSelectd) {
                swiper.src = src;
                selectedItem = swiper;
            }
        });

        // upload image
        if (src) {
            this.swiperSlides.push(Model.PlusButtonItem);
            this.swiperSlides = Array.from(new Set(this.swiperSlides));
        } else {
            // delete image
            this.swiperSlides.splice(selectedItem.index, 1);

            // sort out index after delete image
            (this.swiperSlides || []).forEach((element: Model.ISwiper, arrayIndex: number) => {
                if (element.index !== Model.LastIndex) {
                    element.index = arrayIndex;
                }

                // keep the focus position unchanged
                if (element.index === selectedItem.index) {
                    element.isSelectd = true;
                    this.imageItem.selectedImage = element.src;
                }
            });
        }

        // delete from image 2 to the back, last element is plus button, auto focus to prev one
        if (this.swiperSlides.length === 2 && this.swiperSlides[0].src && this.swiperSlides[1].index === Model.LastIndex) {
            this.imageItem.selectedImage = this.swiperSlides[0].src;
            this.swiperSlides[0].isSelectd = true;
        }

        // delete from image middle to the back, last element is plus button, auto focus to prev one
        if (this.swiperSlides.length > 2) {
            let tempIsSelected: boolean[] = (this.swiperSlides || []).map((element: Model.ISwiper) => element.isSelectd);
            if (Object.values(tempIsSelected).every((x) => !x)) {
                this.swiperSlides[this.swiperSlides.length - 2].isSelectd = true;
                this.imageItem.selectedImage = this.swiperSlides[this.swiperSlides.length - 2].src;
            }
        }

        //  deleted to only plus button
        if (this.swiperSlides.length === 1 && this.swiperSlides[0].index === Model.LastIndex) {
            this.swiperSlides = [Model.FirstSwiperItem];
            this.swiperSlides[0].src = personEmpty;
        }

        // console.log('final => ', this.swiperSlides);
        // console.log(' =========================================== ');

        // emit swiperSlides
        if (this.swiperSlides.length === 2 && this.swiperSlides[0].src !== personEmpty) {
            const porpValue: string[] = [this.swiperSlides[0].src];
            this.$emit('upload-images', porpValue);
        } else if (this.swiperSlides.length > 2) {
            const porpValue: string[] = (this.swiperSlides || []).map((x: Model.ISwiper) => x.src);
            porpValue.pop();
            this.$emit('upload-images', porpValue);
        } else if (this.swiperSlides.length === 1 && this.swiperSlides[0].src === personEmpty) {
            const porpValue: string[] = [];
            this.$emit('upload-images', porpValue);
        }
    }
    //#endregion form function

    //#region resolve text
    private resolvePhotoIsUrlOrBase64(data: string) {
        return ServiceResolveText.resolvePhotoIsUrlOrBase64(data);
    }
}
