








































































































































import { Vue, Component, Prop, Emit, Model, Watch } from 'vue-property-decorator';

import { ServiceBase64, personEmpty, imageEmpty } from '@/../helpers';
import { IComponent } from '@/../models';

import FormImageCropper from './image_cropper.vue';

// vue-easy-lightbox
import Lightbox from 'vue-easy-lightbox';
Vue.use(Lightbox);

interface ILightboxItem {
    imgs: string;
    visible: boolean;
    index: number;
}

@Component({
    components: { FormImageCropper },
})
export default class FormImageAdvancedCropper extends Vue {
    //////////////////// prop ////////////////////

    @Prop({
        type: String,
        default: '',
    })
    private id: string;

    @Prop({
        type: String,
        default: personEmpty,
    })
    private src: string;

    @Prop({
        type: String,
        default: personEmpty,
    })
    private defaultSrc: string;

    @Prop({
        type: Boolean,
        default: true,
    })
    private isModify: boolean;

    @Prop({
        type: Number,
        default: 150,
    })
    private width: number;

    @Prop({
        type: Number,
        default: 150,
    })
    private height: number;

    //////////////////// variable ////////////////////

    private personEmptyDefault: string = personEmpty;
    private faceImgSrc: string = this.personEmptyDefault;
    private isShowToolbox: boolean = false;
    private isShowOriginalImage: boolean = false;

    private lightboxItem: ILightboxItem = {
        imgs: '', // Img Url , string or Array
        visible: false,
        index: 0, // default: 0
    };

    private modalShow: boolean = false;
    private dragging: boolean = false;

    private imageUrl: string = '';

    private cropItem: IComponent.ICropItem = {
        size: 1, //剪切後的圖片質量（0.1 - 1）
        outputType: 'png', // 裁剪生成圖片的格式 jpeg (jpg 需要传入jpeg) || png || webp

        full: true, //是否輸出原圖比例的截圖

        canScale: false, // 圖片是否允許滾輪縮放
        canMove: true, // 上傳圖片是否可以移動
        canMoveBox: true, // 截圖框能否拖動

        autoCrop: false, // 是否默認生成截圖框
        autoCropWidth: 500, // 默認生成截圖框寬度（0 - max）
        autoCropHeight: 500, // 默認生成截圖框高度（0 - max）

        fixed: true, // 是否開啟截圖框寬高固定比例
        fixedNumber: [1, 1], // 截圖框的寬高比例 [寬度, 高度]
        fixedBox: false, // 固定截圖框大小 不允許改變

        original: false, // 上傳圖片按照原始比例渲染

        centerBox: true, // 截圖框是否被限制在圖片裡面

        info: true, // 裁剪框的大小信息
        infoTrue: true, // true 為展示真實輸出圖片寬高 false 展示看到的截圖框寬高

        maxImgSize: 2000, // 0 - max 限制圖片最大寬度和高度
    };

    //////////////////// vue life ////////////////////

    private created(): void {}

    private mounted(): void {
        this.initSrc();
    }

    @Watch('src', { immediate: true, deep: true })
    private formSrcChanged(newVal: string, oldVal: string): void {
        this.initSrc();
    }

    @Watch('defaultSrc', { immediate: true, deep: true })
    private defaultSrcChanged(newVal: string, oldVal: string): void {
        this.personEmptyDefault = newVal;
    }

    //////////////////// Init Data ////////////////////
    private initSrc(): void {
        this.faceImgSrc = this.src;
    }

    //////////////////// cropper ////////////////////

    private getCropperValue(value: string) {
        this.imageUrl = value;
        this.faceImgSrc = value;
        this.clickCloseModal();
        this.$emit('upload-image', this.faceImgSrc, this.id);
    }

    //////////////////// show Icon ////////////////////
    private showToolbox(isShow): void {
        this.isShowToolbox = isShow;
    }

    //////////////////// upload ////////////////////

    // 點擊觸發 input 的 ref
    private clickUploadFile(): void {
        (this.$refs.uploadFile as any).click();
    }

    // 點擊 input 後 上傳檔案
    private checkUploadFile(e): void {
        var files = e.target.files || e.dataTransfer.files;
        let uploadFileRef: any = this.$refs.uploadFile;
        this.uploadFile(uploadFileRef.files[0]);
        uploadFileRef.value = '';
        this.isShowToolbox = false;
    }

    // 處理 file to base64
    private async uploadFile(file): Promise<void> {
        let base64: any = null;
        base64 = await ServiceBase64.getBase64(file);

        if (base64.result) {
            this.faceImgSrc = base64.result;
            this.imageUrl = base64.result;
        }
        this.clickShowModal();
    }

    //////////////////// delete ////////////////////
    private clickDeleteFile(): void {
        this.faceImgSrc = this.personEmptyDefault;
        this.$emit('upload-image', undefined, this.id);
    }

    //////////////////// download ////////////////////
    //下載圖片
    private clickDownloadFile(): void {
        this.downloadFile(this.faceImgSrc);
    }

    //檔案下載
    private async downloadFile(content: string, fileName: string = 'photo.png'): Promise<void> {
        let aLink = document.createElement('a');

        let base64Img: string = '';

        if (ServiceBase64.isUrl(content)) {
            base64Img = await ServiceBase64.urlToBase64(content);
        } else if (ServiceBase64.isBase64Image(content)) {
            base64Img = content;
        }

        //new Blob([content]);
        let blob = this.base64ToBlob(base64Img);

        let evt = document.createEvent('HTMLEvents');
        //initEvent 不加後兩個參數在FF下會報錯  事件類型，是否冒泡，是否阻止瀏覽器的默認行為
        evt.initEvent('click', true, true);
        aLink.download = fileName;
        aLink.href = URL.createObjectURL(blob);

        // aLink.dispatchEvent(evt);
        aLink.click();
    }

    // base64轉blob
    private base64ToBlob(code: string): Blob {
        let parts = code.split(';base64,');
        let contentType = parts[0].split(':')[1];
        let raw = window.atob(parts[1]);
        let rawLength = raw.length;

        let uInt8Array = new Uint8Array(rawLength);

        for (let i = 0; i < rawLength; ++i) {
            uInt8Array[i] = raw.charCodeAt(i);
        }
        return new Blob([uInt8Array], { type: contentType });
    }

    //////////////////// view original ////////////////////
    private clickViewOriginal(): void {
        this.isShowOriginalImage = true;
        this.lightboxItem = {
            imgs: this.faceImgSrc, // Img Url , string or Array
            visible: true,
            index: 0, // default: 0
        };
    }

    private clickHide(): void {
        this.lightboxItem.visible = false;
        this.isShowOriginalImage = false;
    }

    //////////////////// modalSh ////////////////////

    private clickShowModal(): void {
        this.modalShow = true;
        this.dragging = false;
        this.$emit('upload-image', this.faceImgSrc, this.id);
    }

    private clickCloseModal(): void {
        this.modalShow = false;
    }
}
